<div class="player" v-show="videoIsOpen" ref="playerScreen"><div class="layer" :class="[{&quot;full-window&quot;: isFullScreen}]"><div class="close-video" @click="handleVideoOpen"><i class="fas fa-backspace"></i></div><div class="open-full-window" @click="handleFullScreen(),launchIntoFullscreen()"><i :class="[&quot;fas&quot;,{&quot;fa-expand-alt&quot;: !isFullScreen},{&quot;fa-compress-alt&quot;: isFullScreen}]"></i></div></div><div id="player"></div></div><div class="screen" v-show="!videoIsOpen"><img :src="loadVideoCover" alt="alt" @click="handleVideoOpen"><div class="blur-background" :style="backgroundStyle"></div></div><div class="progress-bar"><label>{{formatTime(currentTime)}}</label><div class="progress-content"><input class="bar" type="range" id="duration" name="duration" min="0" :max="duration" step="1" @change="setCurrentTime" v-model.number="currentTime" @mouseover="mouseOver($event)" @mouseleave="mouseLeave" @mousedown="mouseDown($event)" @mouseup="mouseUp"><div class="content"><div class="slider"><div class="track"></div><div class="range" :style="{&quot;left&quot;: `${currentTime/duration*100}%`}"></div><div class="thumb" :style="{&quot;left&quot;: `${currentTime/duration*100}%`}" :class="[{&quot;hover&quot;: hoverId==&quot;duration&quot;},{&quot;active&quot;: activeId==&quot;duration&quot;}]"></div></div></div></div><label>{{formatTime(duration)}}</label></div><div class="control"><button @click="previousVideo"><i class="fas fa-step-backward"></i></button><button @click="stopVideo"><i class="fas fa-stop"></i></button><button @click="playPauseVideo"><i :class="[&quot;fas&quot;,{&quot;fa-play&quot;: buttonPlayPause},{&quot;fa-pause&quot;: !buttonPlayPause}]"></i></button><button @click="nextVideo"><i class="fas fa-step-forward"></i></button><button @click="randomVideo" :class="[&quot;random&quot;,{&quot;active&quot;: isRandom}]"><i class="fas fa-random"></i></button><button @click="oneLoop" title="test" :class="[&quot;loop&quot;,{&quot;active&quot;: isOneLoop}]"><i class="fas fa-undo"></i></button><button @click="handleFullScreen(),launchIntoFullscreen()"><i :class="[&quot;fas&quot;,{&quot;fa-expand-alt&quot;: !isFullScreen},{&quot;fa-compress-alt&quot;: isFullScreen}]"></i></button></div><div class="volume-range"><div class="volume"><button @click="mute"><i :class="[&quot;fas&quot;,{&quot;fa-volume-mute&quot;: volumeRange==1},{&quot;fa-volume-off&quot;: volumeRange==2},{&quot;fa-volume-down&quot;: volumeRange==3},{&quot;fa-volume-up&quot;: volumeRange==4}]"></i></button></div><div class="range-content"><input type="range" id="vol" name="vol" min="0" max="100" step="1" v-model.number="volume" @mouseover="mouseOver($event)" @mouseleave="mouseLeave" @input="changeVolume(volume)" @mousedown="mouseDown($event)" @mouseup="mouseUp"><div class="content"><div class="slider"><div class="track"></div><div class="range" :style="{&quot;left&quot;: `${volume}%`}"></div><div class="thumb" :style="{&quot;left&quot;: `${volume}%`}" :class="[{&quot;hover&quot;: hoverId==&quot;vol&quot;},{&quot;active&quot;: activeId==&quot;vol&quot;}]"></div></div></div></div></div><div class="info"><a class="marquee" :href="info.videoUrl" target="_blank" :style="marqueeAnimate"><h3>{{info.data.title}} </h3></a></div>